<!-- 集市下面的三大主粮组件 -->
<template>
    <div id="SanDaZhuLiang_Cont">
        <!-- <v-headr :myname="myname"></v-headr> -->
        <header class="head-container title-container header_title" >
            <div class="aux_back"  @click="backPrv()"></div>
            <p class="message-title text-cent">{{titleText}}</p>    
        </header>

            <div v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="10"  class="act-bazaar-container"> <!-- wrip-con -->
                <div class="aui-card-list" style="margin-top: 44px">
                        <a  href="javascript:void(0);"
                           class="aui-panel-cell"
                           v-for="key in datas"
                           @click="goInfoDM(key)"
                           > 
                            <div class="aui-panel-cell-bd">
                                <h4>{{key.HxCropofferStr}}</h4>
                                <span>{{key.OfferDate}}</span>
                            </div> 
                            <div class="float-rigt">
                                <em class="jiage">￥{{key.Price}}元</em>
                            </div>
                        </a>
                </div>
            </div>
            <img src="../../assets/img/Loading.gif" class="loading" v-if="loading">
      
    </div>
</template>
<style scoped>
    #Headr{ height: 44px; }
    .loading{
        display: block;
        margin: 0 auto;
    }
    .wrip-con{
        margin-top: 44px;
    }
</style>
<script type="text/javascript">
    import Headr from '../../components/header.vue'
    export default {
        name:'SanDaZhuLiang_Cont',
        data(){
            return { 
                isA:0,
                msg:'集市下面的三大主粮组件',
                myname:{
                    name:'三大主粮333',
                    IsSubPage:true
                },
                titleText:'',
                datas:[],
                busy:false,
                loading:true,
                PerPageNum:20,
                PageNum:0,
                OfferDateStart:'2018-7-16',
                backLength:0,
                
            }
        },
        components:{
           /* 'v-headr':Headr,*/
        },
        methods:{
             /*获取地区*/
            getRegion(){
                this.axios.get('/api/flxoa/Region',{
                    params:{
                        RegionID:0,
                    }
                }).then(res => {
                    console.log("获取地区成功"+res)
                    //let data = res.data[0].data
                    //this.daogu = data
                    console.log(JSON.stringify(res))
                    //console.log(res.data[0].data[0])
                  
                },err => {
                     console.log("获取地区失败"+err)
                })
                .catch(err=> {
                    console.log("捕获的错误")
                })
             },
            getLIst(flag){
                 //this.$indicator.open();
                let qStr = this.$route.query.q;
                let from = this.$route.query.from;
                if(qStr == '0209' || qStr == '0212'){
                    this.OfferDateStart = '2018-3-1'
                } else{
                    this.OfferDateStart = '2018-7-16'
                }
                //console.log("接受来自"+from)
                //console.log("接受来自"+qStr)
                this.titleText = this.$route.query.from;
                this.axios.get('/api/flxoa/CropOffer',{
                   params:{
                        PerPageNum:this.PerPageNum,
                        PageNum:this.PageNum,
                        CropCategoryCode:qStr,
                        iscropcategory:1,
                        OfferDateStart:this.OfferDateStart,
                    }

                }).then(res => {
                    console.log("三大主粮更多成功"+res)
                    console.log(res.data[0].data)
                    console.log("FFFFFF"+res.data[0].data.length)
                    this.backLength = res.data[0].data.length

                    if(flag){
                         this.datas =  this.datas.concat(res.data[0].data)
                         
                         if(res.data[0].data.length < this.PerPageNum){
                            this.busy = true
                            this.loading = false
                         }else{
                            this.busy = false
                         }
                    }else{
                        this.datas = res.data[0].data
                        this.busy = false
                    }

                },err => {
                     console.log("三大主粮更多失败"+err)
                })
                .catch(err=> {
                    console.log("捕获的错误")
                })
               
             },
             goInfo:function(){

             },

            loadMore() {
                //alert("++")
                console.log("ccccc"+this.backLength)
                if(this.backLength == 20){  //this.backLength < this.PerPageNum                   
                    this.busy = true;
                     this.getLIst(true);
                      setTimeout(() => {
                        //console.log(this.PageNum)
                        this.PageNum++;
                       // console.log(this.PageNum)
                        this.busy = false;
                      }, 3000);
                }else{
                    this.loading = false
                    return;
    
                }
                 
            },

             goMore:function(q){
                console.log(q);
             },
             tabToggle:function(index){
                 
             },
             backPrv(){
                this.$router.go(-1)
              },
               goInfoDM:function(keys){
                console.log(keys.RegionCode)  
                this.$router.push({ name: 'SanDaZhuLiangXQ', query: { Region: keys.RegionCode,CropCategory: keys.CropCategoryCode,Price:keys.Price,CropOfferStr:keys.HxCropofferStr}})
             }

        },
        mounted(){  
            this.getLIst();
            //this.getRegion();

       



        }
    }
</script>


