<!-- 大的资讯组件 -->
<template>
	<div id="My">
		 <section class="aui-scrollView">
                <div class="aui-head-yellow">
                    <div class="aui-head-box">
                        <div class="aui-head-user">
                            <img src="../../assets/img/5744081.png" width="70" alt="">
                        </div>
                        <a href="javascript:;" v-show="userId==null" @click="goLogin()" class="btnlogin">立即登录</a>
                        <p  v-show="userId!=null">
                        	<h3 class="user-name-tit">
                                <!-- <span class="color2">LV12</span> -->
                                {{userId}}
                            </h3>
                        	<!-- <small class="user-info-tit">航天信息股份有限公司<br>智慧粮农事业部 <img src="../../assets/img/duihao.png" width="12" ></small> -->
                        </p>
                    </div>
                    <div class="head_bolang"></div>
                </div>

                <div class="aui-card-list">
                    <!--  <router-link to="/WoDe/WoDeGuanZhu" class=""> -->
                        <a href="javascript:void(0);" class="aui-panel-cell" @click="islogin('我的关注')">
                            <div class="aui-panel-cell-hd li_1"></div>
                            <div class="aui-panel-cell-bd">
                                <h4>我的关注</h4>
                            </div>
                        </a>
                    <!-- </router-link> -->
                    <!-- <router-link to="/WoDe/WoDeDingYue" class=""> -->
                        <a href="javascript:void(0);" class="aui-panel-cell" @click="islogin('我的订阅')">
                            <div class="aui-panel-cell-hd li_2"></div>
                            <div class="aui-panel-cell-bd">
                                <h4>我的订阅</h4>
                            </div>
                        </a>
                    <!-- </router-link> -->

                    <!-- <router-link to="/WoDe/WeDeShouCang" class=""> -->
                        <a href="javascript:void(0);" class="aui-panel-cell" @click="islogin('我的收藏')">
                            <div class="aui-panel-cell-hd li_3"></div>
                            <div class="aui-panel-cell-bd">
                                <h4>我的收藏</h4>
                            </div>
                        </a>
                    <!-- </router-link> -->
                     
                    <div class="divHeight"></div>
                    <router-link to="/WoDe/WoDeKeFu" class="">
                        <a href="javascript:void(0);" class="aui-panel-cell">
                            <div class="aui-panel-cell-hd li_4"></div>
                            <div class="aui-panel-cell-bd">
                                <h4>联系客服</h4>
                            </div>
                        </a>
                    </router-link>
                    <div class="divHeight"></div>
                    <router-link to="/WoDe/Setting" class="">
                        <a href="javascript:void(0);" class="aui-panel-cell">
                            <div class="aui-panel-cell-hd li_5"></div>
                            <div class="aui-panel-cell-bd">
                                <h4>设置</h4>
                            </div>
                        </a> 
                    </router-link>

                </div>
            </section>
		
	
    	
	</div>




</template>

<script type="text/javascript">
	//import News from '../components/news.vue';
	

	export default {
		name:'My',
		data(){
			return {
				userId:null
			}
		},
		methods:{
			goLogin:function(){
                window.location.href='/Login'
            },
            
            islogin:function(whick){
                var user_id = sessionStorage.userId;
                if(user_id == null){ 
                    this.$messagebox({ 
                      title: '即将登录', 
                      message: '登录后才能订阅、关注、收藏', 
                      showCancelButton: true, 
                      confirmButtonText:"取消", 
                      cancelButtonText:"登录" 
                    }).then(action => { 
                      if(action == 'confirm'){ 
                        console.log('继续购物')
                      }else{
                         this.$router.push({name:"Login"})
                      } 
                  }) 
                    return;
                }else{
                    switch(whick){
                        case "我的关注":
                            this.$router.push({name:"WoDeGuanZhu" ,params:{Iam:"我的关注"}})
                        break;
                        case "我的订阅":
                            this.$router.push({name:"WoDeDingYue"})
                        break;
                        case "我的收藏":
                            this.$router.push({name:'WeDeShouCang'})
                        break;
                    }
                    
                }

            },
            






		},
		components:{
			
			
			

		},
		mounted(){
			
            var user_id = sessionStorage.userId;
                if(user_id != null){
                    this.userId = user_id;    
                }
                


		}




	}
</script>

<style type="text/css" >
	a{color: inherit}
    .btnlogin { font-size:14px; color:#0dc751; background:#fff; border-radius:5px;padding:5px 10px; text-align:center;}
</style>


