<!-- 我的我的预定组件 -->
<template>
    <div id="WoDeDingYue">
        <v-headr :myname="myname"></v-headr>
        <div class="my-subscription-container">
        <ul class="my-subscription-warp" v-for="(item,key) in DingYueData">
            <li class="my-subscription-list bod-list clearfix">
                <div class="subscription-pic float-lft">
                    <img class=" width100 " src="../../assets/img/dingyue-pic1.png"/>
                </div>                
                <div class="bod-list-info float-lft">
                    <div class="subscription-wrap" @click="goInfo(item.RegionCode,item.CropCategoryCode,item.Price,item.CropCategoryName)">
                        <span class="subscription-city displ-inl-blk">{{item.Region}}</span>
                        <p class="subscription-name displ-inl-blk">{{item.CropCategoryName}}</p>
                        <p class="subscription-num"></p>
                    </div>
                   
                </div>
                <div class="my-subscriptioned float-rigt">
                     <p @click="removeSubscribe(item)" class="float-rigt subscriptioned text-cent">✓&nbsp;已订阅</p>
                </div>
            </li>
            
        </ul>
    </div>


    <!-- <div class="add-more-con">
        <a class="add-more" href="javascript:;"  @click="addMor()">
            <span class="add-pic"></span>
            <span class="add-btu">添加更多</span> 
        </a>
    </div> -->
    </div>
</template>

<script type="text/javascript">
    import Headr from '../../components/header.vue'
    var user_id = sessionStorage.userId; 
    export default {
        name:'WoDeDingYue',
        data(){
            return {
                msg:'我的订阅组件',
                myname:{
                    name:'我的订阅',
                    IsSubPage:true
                },
                DingYueData:null
               
            }
        },
        /*props:{
            fromjishi
        },*/
        methods:{
            getData(){
                this.DingYueData = DingYue.result[0].WoDe; 
            },
            addMor(){
                this.$router.push({ name: 'GeRenDingYue'})
            },
            init_DY(){
                var scThis = this;  
                this.axios.get('/api/flxoa/Subscribe').then(function(response){ 
                    console.log('--------------------我的-订阅---------------------')
                    console.dir(response.data[0].data);
                    scThis.DingYueData = response.data[0].data;
                });  
            },
            removeSubscribe(item){
                //console.log(item.SubscribeID)
                var scThis = this;  
                this.axios.get('/api/flxoa/SubscribeDel?SubscribeID='+item.SubscribeID).then(function(response){ 
                    //console.log('删除-我的-订阅'+response)
                    //console.log('删除-我的-订阅'+JSON.stringify(response) )
                    if(response.data.code == 200){
                        scThis.init_DY();
                        scThis.$toast('取消成功');
                    }
                });    
            },
            goInfo:function(RegionCode,CropCategoryCode,Price,CropCategoryName){
                this.$router.push({ name: 'SanDaZhuLiangXQ', query: { Region: RegionCode,CropCategory: CropCategoryCode,Price:Price, CropOfferStr:CropCategoryName}})
            },
           
            
            
            



          

        },
        components:{
            'v-headr':Headr,
        },
        mounted(){
            //this.islogin() 
            this.init_DY();
        }
    }
</script>


