<!-- 导航下的我的关注页面 -->
<template>
	<div id="LaiYuan">
		<v-headr :myname="myname"></v-headr>
		<div class="my-concern-container">
	        <ul class="my-concern-warp aui-card-list">
	            <li  v-for="item in datas"
	            class="my-concern-list aui-panel-cell ">
	                <div class="my-concern-pic "></div>
	                <div class="aui-panel-cell-bd">
	                    <h4>{{item.SourceName}}</h4>
	                </div>
	                <div class="right_tex my-concerned col-fe"
	                	:class="{'guanzhu':item.flag == 1}"
	                 @click="goFollow(item,item.flag)">{{item.flag　== 1 ? '已关注' : '关注'}}</div>
	            </li> 
	        </ul>
	    </div>



	   
	</div>
</template>

<script type="text/javascript">
	import Headr from '../../components/header.vue' 
	export default {
		name:'LaiYuan',
		data(){
			return {
				msg:'我的关注来源',
				myname:{
                    name:'关注来源',
                    IsSubPage:true
                },
				datas:null, 
			}
		},
		components:{
			'v-headr':Headr,
		},
		methods:{
			goFollow:function(items,flag){
				console.log(flag);
				if(flag==0){
					this.followPost(items.SourceCode); 
				}else{
					this.followRemove(items.FollowID);
				}
				
			},

			followPost:function(sorcId){ 
				console.log("关注"); var this_ = this;
				console.log("....."+sorcId)
				this.axios.get('/api/flxoa/FollowAdd?SourceCode='+sorcId)
		          .then(function (response) {
		        	this_.InitLaiYuanList()
		        	console.log('关注成功');
		        	this_.$toast("关注成功");
		            //console.log(response);  
		          })
		          .catch(function (error) {
		              console.log(error);
		          }); 
			},
			followRemove:function(sorcId){ 
				var this_ = this;
				console.log("接到的取消ID"+sorcId)
				this.axios.get('/api/flxoa/FollowDel?SourceCode='+sorcId)
		          .then(function (response) {
		        	//console.log("取消关注后的返回"+response)
		        	//console.log("取消关注后的返回"+JSON.stringify(response) )
		        	if(response.data.Msg == "成功"){
		        		this_.InitLaiYuanList()
		        		this_.$toast("取消关注成功");
		        	}
		        	 
		          })
		          .catch(function (error) {
		              console.log(error);
		          }); 
			},



			InitLaiYuanList(){
				var user_id = sessionStorage.userId; 
				this.axios.get('/api/flxoa/FollowMore')
				.then(response => { 
					console.log('--------------------来源列表数据---------------------'+response)
					console.dir(response.data[0].data);
	                this.datas = response.data[0].data;
	            });  
			},
			



		},
		mounted(){
           this.InitLaiYuanList()


		},
		created(){

		},


	}
</script>
<style type="text/css" scoped>
	.col-fe{ border: 1px solid #febe22; color: #febe22; width: 36px; text-align: center;}
	.guanzhu {border: 1px solid #7b7d7f;color: #6a6a6a; }

</style>


