'3220','3200','3200','3150','3170','3170','3130','3150','3130','3090','3100','3100','3140','3170','3150','3120','3090','3170','3190','3170','3200','3350','3300','3260','3250','3250','3200','3180','3200','3200','3200','3200','3180','3180','3150','3110','3160','3160','3120','3100','3020','2990','3000','2980','2960','2940','2880','2860','2850','2850','2850','2870','2900','2920','2900','2920','2920','2880','2900','2900','2880','2870','2850','2800','2800','2800','2800','2820','2840','3000','3000','2970','2900','2900','2920','2960','3010','3020','3080','3080','3080','3080','3080','3100','3080','3100','3100','3060','3060','3060','3060','3060','3080','3100','3080','3100','3120','3100','3120','3150','3120','3120','3120','3140','3170','3190','3280','3290','3220','3230','3280','3280','3260','3210','3210','3210','3210','3200','3170','3160','3170','3220','3220','3250','3250','3240','3220','3240','3300','3300','3300','3290','3270','3300','3310','3310','3310','3340','3400','3430','3430','3500','3580','3620','3630','3650','3600','3600','3610','3610','3630','3630'],
						type: 'line',
