		},
		methods:{
			/*获取当前时间*/
		    getNowFormatDate() {
		        var date = new Date();
		        var seperator1 = "-";
		        var year = date.getFullYear();
		        var month = date.getMonth() + 1;
		        var strDate = date.getDate();
		        if (month >= 1 && month <= 9) {
		            month = "0" + month;
		        }
		        if (strDate >= 0 && strDate <= 9) {
		            strDate = "0" + strDate;
		        }
		        var currentdate = year + seperator1 + month + seperator1 + strDate;
		        return currentdate;
			},
			/*上个月时间*/
			makeLastMonthDate(){
				var nowdate = new Date();
			    nowdate.setMonth(nowdate.getMonth()-1);
			    var y = nowdate.getFullYear();
			    var m = nowdate.getMonth()+1;
			    var d = nowdate.getDate();
			    var formatwdate = y+'-'+m+'-'+d;
			    return formatwdate			   
			},
			/*取前一年的日期*/
			makeLastYear(n){
				let oneYear;
				let y = this.getNowFormatDate().substring(0,4)
				let oth = this.getNowFormatDate().substring(4)
					y = y - n
				 return oneYear = y+""+oth
			},
			/*抽取图标所需的数据*/
            extractChartData(obj,target){
                let Arr = [];
                    for(let i=0; i<obj.length; i++){
                        Object.keys(obj[i]).forEach(function(key,value){
                            if(key == target){
                                Arr.push(obj[i][key])
                            }
                           // console.log(key,obj[i][key]);
                        });
                    }
                //console.log(Arr)
                return Arr
            },

            extractChartData2(obj,target){
            	console.log(obj)
            	const charArr = obj[0].data; 
            	var r = charArr.filter(function (element, index, self) { 
            		if(element.RegionCode == target) 
					return element; 
				});	 
				 //console.log(r) 
				 return r
            },
            /*截取X轴日期*/
            makeDateX(obj){
                let dateX = []
               for(let i=0; i<obj.length; i++){
                    dateX.push(obj[i].substring(5).replace("-","/"))
               }
               return dateX
            },
            /*取数组最小值*/
            getMix (obj){
                let mixArr =  Math.min.apply(null, obj);
                return mixArr 
            },
            //get方法集成
            getdata:function(url,params={}){
		        return new Promise((resolve,reject) => {
		          this.axios.get(url,{
		            params:params
		          })
		          .then(response => {
		            resolve(response.data);
		          })
		          .catch(err => {
		            reject(err)
		          })
		        })
		 
		       },
            /*请求接口
            	domi 传入的图表DOM 容器
            	option 图表的数据对象
            	width 区分时间段 用于改变图表的样式
            */
            requestInterface(domId,option,width){
            	let params = {
                        regionList:'0538,0012,0010',   //0153,0083,0113
                        CropCategoryCode:'0538',
                        OfferDateStart:this.OfferDateStart,
                        OfferDateEnd:this.OfferDateEnd,
                    } 
                    this.$indicator.open();


                    this.getdata('/api/flxoa/CropOfferIcon',params).then((res) => {
            

                	this.$indicator.close();
                	console.dir(res)
                	var data =  res[0].data;
                	console.log("图表返回数据"+JSON.stringify(res) )
                	let mixCon = []
                	let RegArr1 = this.extractChartData2(res,'0012')  //分类 三个地区
                	let RegArr2 = this.extractChartData2(res,'0012')
                	let RegArr3 = this.extractChartData2(res,'0010')
                	console.log("过滤后的")
                	console.dir(RegArr1)
                	mixCon.push(this.getMix(this.extractChartData(RegArr1,'Price'))) // 取最小值
                	mixCon.push(this.getMix(this.extractChartData(RegArr2,'Price')))
                	mixCon.push(this.getMix(this.extractChartData(RegArr3,'Price')))
                	console.log("过滤后的价格"+this.getMix(this.extractChartData(RegArr1,'Price')))
                	if(width == '3' || width == '4' || width == '5'){
                		this.optionF.yAxis.data = this.extractChartData(RegArr1,"OfferDate")
	                	this.optionF.series[0].data = this.extractChartData(RegArr1,'Price')
	                	this.optionF.series[1].data = this.extractChartData(RegArr2,'Price')
	                	this.optionF.series[2].data = this.extractChartData(RegArr3,'Price')               	
	                	this.optionF.xAxis.min = this.getMix(mixCon)
	                	this.optionF.legend.data[0] = RegArr1[0].RegionName
	                	this.optionF.legend.data[1] = RegArr2[0].RegionName
	                	this.optionF.legend.data[2] = RegArr3[0].RegionName
	                	this.optionF.series[0].name = RegArr1[0].RegionName 
						this.optionF.series[1].name = RegArr2[0].RegionName
						this.optionF.series[2].name = RegArr3[0].RegionName

                	}else{
                		this.option.xAxis.data = this.extractChartData(RegArr1,"OfferDate")
	                	this.option.series[0].data = this.extractChartData(RegArr1,'Price')
	                	this.option.series[1].data = this.extractChartData(RegArr2,'Price')
	                	this.option.series[2].data = this.extractChartData(RegArr3,'Price')               	
	                	this.option.yAxis.min = this.getMix(mixCon)
	                	console.log(RegArr3)
	                	this.option.legend.data[0] = RegArr1[0].RegionName
	                	this.option.legend.data[1] = RegArr2[0].RegionName
	                	this.option.legend.data[2] = RegArr3[0].RegionName
	                	this.option.series[0].name = RegArr1[0].RegionName 
						this.option.series[1].name = RegArr2[0].RegionName
						this.option.series[2].name = RegArr3[0].RegionName
                	}
                	
                	if(width == '0'){
						this.option.xAxis.axisLabel.interval = 2
						this.drawLine(domId,option);               		
                	}else if(width == '1'){
						this.option.xAxis.axisLabel.interval = 30
						this.drawLine(domId,option);

                	}else if(width == '2'){
						this.option.xAxis.axisLabel.interval = 40
						this.drawLine(domId,option);
                	}else if(width == '3'){
                		//alert("放大")
						this.option.xAxis.axisLabel.interval = 20
						this.drawLine(domId,option);
                	}else if(width == '4'){
                		//alert("放大")
						//this.option.xAxis.axisLabel.interval = 40
						this.drawLine(domId,option);
                	}else if(width == '5'){
						this.option.xAxis.axisLabel.interval = 100
						this.drawLine(domId,option);
                	}

                


          });



                //     this.axios.get('/api/flxoa/CropOfferIcon',{
                // 	params:params,
                // })
                // .then(res => {})
            }, 

           
			/*分不同时间请求数据
				0表示一个月
				1表示一年
				2表示两年
				3、4、5 用于全屏时请求不同时间段的数据
			*/
            getChartData(timeQuantum){ 
            	if(timeQuantum == "0"){
            		this.fullitem = '3'
            		this.oneMonth = true,
				    this.oneYear = false,
            		this.twoYear = false,	
            		this.OfferDateStart = this.makeLastMonthDate()
            		this.requestInterface('chart',this.option,'0')

            	}else if(timeQuantum == "1"){
            		this.fullitem = '4'
            		this.twoYear = false,
				    this.oneMonth = false,
				    this.oneYear = true,
            		this.OfferDateStart = this.makeLastYear(1)
            		this.requestInterface('chartyn',this.option,'1')
            	}else if(timeQuantum == "2"){
            		this.fullitem = '5'
            		this.oneMonth = false,
				    this.oneYear = false,
            		this.twoYear = true,
            		this.OfferDateStart = this.makeLastYear(2)
            		this.requestInterface('chartln',this.option,'2')
            	}else if(timeQuantum == "3"){ //全屏时候的一月 一年 两年
            		this.OfferDateStart = this.makeLastMonthDate()
            		this.requestInterface('full_chart',this.optionF,'3')
            	}else if(timeQuantum == "4"){
            		//alert("一年")
            		this.OfferDateStart = this.makeLastYear(1)
            		this.requestInterface('full_chart',this.optionF,'4')
            	}else if(timeQuantum == "5"){
            		this.OfferDateStart = this.makeLastYear(2)
            		this.requestInterface('full_chart',this.optionF,'5')
            	}
            },
            
			drawLine(domId,option) {
		      // 基于准备好的dom，初始化echarts实例
		      let myChart = echarts.init(document.getElementById(domId))
		      myChart.setOption(option);
		      setTimeout(function (){
				    window.onresize = function () {
				        myChart.resize();
				        //alert(666)
				    }
			  },2000)
				
		    },

		    /*点击放大图标*/
		    magnify(){
		    	this.fullChart = true
		    	this.getChartData(this.fullitem);
		    },
		    CloseFull(){
		    	this.fullChart = false
		    },

		    tabToggle(index){
				this.tabIndex = index
				this.getChartData(index)
			},



		},
		components:{
			'v-headr':Headr,
		},
		mounted(){
			let h = "";
	    	let w = "";
			w = document.body.clientWidth;
			h = document.body.clientHeight;
			this.w = w;
			this.h = 2000;
		},
		created(){
			this.OfferDateStart = this.makeLastMonthDate() // 获取前一个月日期
			this.OfferDateEnd = this.getNowFormatDate() //获取当前日期
			this.getChartData('0')
		},





	}


</script>
